
import Search from 'vux/src/components/search/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
Search,
InlineLoading
},
data() {
return {
dataobj: {
"ZZlist": [ //症状列表
],
"YPlist": [ //药品列表
],
"JBlist": [ //疾病列表
]
},
value: '', //输入框值
nodata: false, //无搜索结果
history: '', //历史记录
historyShow: false, //历史记录展示
pageIndex: 1,
pageSize: 15,
};
},
mounted() {
this.getHistory();
this.$refs.searchinput.focus() 
},
watch: {
value: function() {    //搜索框输入限制
this.value =  this.value.replace(/[^A-Za-z0-9\u4e00-\u9fa5]/g, '');
}
},
methods: {

removeValue() {
this.value = '';
this.$refs.searchinput.focus();
this.nodata = false;
this.dataobj = {
"ZZlist": [],
"YPlist": [],
"JBlist": []
};
},

showHistoryJuge() {
if (this.value.toString().length < 1 && this.history.length > 0) {
this.historyShow = true;
} else {
this.historyShow = false;
}
},

getHistory() {
if (window.localStorage.history_home) {
this.history = window.localStorage.history_home
let noRepeatArry = this.history.split(',');
this.history = [...new Set(noRepeatArry.reverse())].reverse().join();
if (this.history.charAt(0) == ',') {
this.history = this.history.substring(1, this.history.length)
}
if (this.history.charAt(this.history.length - 1) == ',') {
this.history = this.history.substring(0, this.history.length - 1)
}
localStorage.setItem('history_home', this.history)
} else {
localStorage.setItem('history_home', '')
}
},

removeHistory() {
this.history = '';
localStorage.setItem('history_home', '')
this.showHistoryJuge()
},

setHistory() {
this.historyShow = false;
if (this.value != '') {
let noRepeatArry = this.history.split(',');
noRepeatArry.push(this.value);
this.history = [...new Set(noRepeatArry.reverse())].reverse().join();
if (this.history.charAt(0) == ',') {
this.history = this.history.substring(1, this.history.length)
}
if (this.history.charAt(this.history.length - 1) == ',') {
this.history = this.history.substring(0, this.history.length - 1)
}
localStorage.setItem('history_home', this.history)
}
},

setseartch(val) {
this.value = val;
this.initdata();
},

initdata() {
this.value =  this.value.replace(/[^A-Za-z0-9\u4e00-\u9fa5]/g, '');
this.showHistoryJuge()
if (this.value != '') {
this.$ajax.post(this.$root.host, {
'MSH.1': 'homeSearch',
'MSH.2': 'advancedSearch',
'searchContent': this.value,
'pageNum': this.pageIndex,
'pageSize': this.pageSize,
}).then(data => {
console.log(data)
if (typeof data != 'undefined') { //判断是否空对象
this.dataobj = Object.assign(this.dataobj, data);
if (this.dataobj.ZZlist.length == 0 && this.dataobj.YPlist.length == 0 && this.dataobj.JBlist.length == 0) {
this.nodata = true;
} else {
this.nodata = false;
}
} else {
this.nodata = true;
}
}).catch(error => {
console.log(error);
});
} else {
this.dataobj = {
"ZZlist": [],
"YPlist": [],
"JBlist": []
};
this.nodata = false;
}
},
toNextSearch(type) { //点击跳转
this.$router.push({
name: "searchNext",
params: {
type: type, //展示类
value: this.value, //当前搜索值
}
});
},
toDetail(url, name, code) { //点击跳转
this.$router.push({
name: url,
params: {
code: code,
name: name,
}
});
},
getfucs(){
this.$refs.searchinput.focus() 
}
}
};



